@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@100&display=swap);
body {
 font-family: "Heebo", sans-serif;
 margin: 0;
 padding: 0;
 background: url(/TS-Game/static/media/background.80b6e997.jpg) no-repeat center center fixed;
 background-size: cover;
 display: flex;
 justify-content: center;
 align-items: center;
 width: 100%;
 height: 100vh;
}

.app-container {
 display: flex;
}

.button-6 {
 align-items: center;
 background-color: #ffffff;
 border: 1px solid rgba(0, 0, 0, 0.1);
 border-radius: 0.25rem;
 box-shadow: rgba(0, 0, 0, 0.02) 0 1px 3px 0;
 box-sizing: border-box;
 color: rgba(0, 0, 0, 0.85);
 cursor: pointer;
 display: inline-flex;
 font-family: system-ui, -apple-system, system-ui, "Helvetica Neue", Helvetica,
  Arial, sans-serif;
 font-size: 16px;
 font-weight: 600;
 justify-content: center;
 line-height: 1.25;
 margin: 10px;
 min-height: 3rem;
 padding: calc(0.875rem - 1px) calc(1.5rem - 1px);
 position: relative;
 text-decoration: none;
 transition: all 250ms;
 -ms-user-select: none;
     user-select: none;
 -webkit-user-select: none;
 touch-action: manipulation;
 vertical-align: baseline;
 width: auto;
}

.button-6:hover,
.button-6:focus {
 border-color: rgba(0, 0, 0, 0.15);
 box-shadow: rgba(0, 0, 0, 0.1) 0 4px 12px;
 color: rgba(0, 0, 0, 0.65);
}

.button-6:hover {
 transform: translateY(-1px);
}

.button-6:active {
 background-color: #f0f0f1;
 border-color: rgba(0, 0, 0, 0.15);
 box-shadow: rgba(0, 0, 0, 0.06) 0 2px 4px;
 color: rgba(0, 0, 0, 0.65);
 transform: translateY(0);
}

.start-page-title {
 text-align: center;
 font-size: 30px;
 color: rgb(204, 204, 204);
 animation-iteration-count: infinite;
 animation-duration: 6s;
 animation-name: in-out;
 animation-timing-function: ease-out;
}

@keyframes in-out {
 0% {
  transform: scale(1);
 }
 50% {
  transform: scale(1.5);
 }
 100% {
  transform: scale(1);
 }
}

.basket {
 width: 100px;
 height: 100px;
 border-radius: 10px;
 margin: 0 40px;
 display: flex;
 flex-direction: column;
 text-align: center;
 justify-content: center;
 align-items: center;
 -webkit-user-select: none;
     -ms-user-select: none;
         user-select: none;
}
.shark-bed {
 width: 100px;
 height: auto;
}

.board {
 position: relative;
 width: 500px;
 height: 500px;

 border-radius: 10px;
 box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px,
  rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
}

.random-object {
 width: 30px;
 height: 30px;
 border-radius: 10px;
 position: absolute;
 cursor: grab;
}

.main-shark-0 {
 width: 40px;
 height: auto;
 animation: 3s ease-out 0s 1 ease-in1;
}
.main-shark-1 {
 width: 40px;
 height: auto;
 animation: 5s ease-out 0s 1 ease-in2;
}
.main-shark-2 {
 width: 40px;
 height: auto;
 animation: 2.5s ease-out 0s 1 ease-in3;
}
.main-shark-3 {
 width: 40px;
 height: auto;
 animation: 1.5s ease-out 0s 1 ease-in4;
}

@keyframes ease-in1 {
 0% {
  transform: translateX(-100%);
 }
 100% {
  transform: translateX(0);
 }
}
@keyframes ease-in2 {
 0% {
  transform: translateX(0);
 }
 100% {
  transform: translateX(-100%);
 }
}
@keyframes ease-in3 {
 0% {
  transform: translateX(-100%);
 }
 100% {
  transform: translateX(0);
 }
}
@keyframes ease-in4 {
 0% {
  transform: translateX(0);
 }
 100% {
  transform: translateX(-100%);
 }
}

.stats {
 height: 300px;
 width: 150px;
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 font-size: 40px;

 background: rgba(255, 255, 255, 0.15);
 box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
 backdrop-filter: blur(10px);
 -webkit-backdrop-filter: blur(10px);
 border-radius: 10px;
 border: 1px solid rgba(255, 255, 255, 0.18);
}

.timer-title {
 font-size: 15px;
}

.finish-page {
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
}

.finish-page-title {
 text-align: center;
 font-size: 30px;
 color: rgb(204, 204, 204);
}
.finish-page-result {
 color: chartreuse;
}

